:host {
  display: flex;
  flex: 0 0 auto;
  align-items: center;
  max-width: fit-content;
  color: var(--pfe-clipboard--Color, var(--pfe-broadcasted--link, #06c)) !important;
  cursor: pointer;
  padding: var(--pfe-clipboard--Padding, 6px 16px);
  font-weight: var(--pfe-clipboard--FontWeight, var(--pfe-theme--font-weight--light, 300));
  font-size: var(--pfe-clipboard--FontSize, var(--pf-global--FontSize--md, 1rem));
}

:host([hidden]) {
  display: none;
}

.pfe-clipboard__icon {
  display: flex;
  width: var(--pfe-clipboard--icon--Width, var(--pfe-theme--icon-size, 1em));
  height: var(--pfe-clipboard--icon--Height, auto);
  margin: var(--pfe-clipboard--icon--Margin, 0 0.4825rem 0 0);
  --pfe-icon--Color: var(--pfe-clipboard--icon--Color, var(--pfe-theme--color--text--muted, #6a6e73));
}

.pfe-clipboard__icon svg {
  fill: var(--pfe-clipboard--icon--Color, var(--pfe-theme--color--text--muted, #6a6e73)) !important;
}

:host([disabled]) .pfe-clipboard__icon {
  opacity: 0.4;
}

.pfe-clipboard__text {
  color: var(--pfe-clipboard--Color, var(--pfe-broadcasted--link, #06c)) !important;
}

:host([disabled]) .pfe-clipboard__text {
  color: var(--pfe-clipboard--Color--disabled, var(--pfe-theme--color--ui-disabled--text, #6a6e73)) !important;
}

.pfe-clipboard__text--success {
  color: var(--pfe-clipboard--text--success--Color, var(--pfe-theme--color--feedback--success, #3e8635)) !important;
}

:host(:hover:not([disabled])) .pfe-clipboard__text,
:host(:focus:not([disabled])) .pfe-clipboard__text {
  color: var(--pfe-clipboard--Color--hover, var(--pfe-broadcasted--link--hover, #004080)) !important;
}

:host(:hover:not([disabled])) .pfe-clipboard__icon,
:host(:focus:not([disabled])) .pfe-clipboard__icon {
  --pfe-icon--Color: var(--pfe-clipboard--icon--Color--hover, var(--pfe-theme--color--ui-base--hover, #151515));
}

:host(:hover:not([disabled])) .pfe-clipboard__icon svg,
:host(:focus:not([disabled])) .pfe-clipboard__icon svg {
  fill: var(--pfe-clipboard--icon--Color--hover, var(--pfe-theme--color--ui-base--hover, #151515)) !important;
}

:host([copied]) .pfe-clipboard__text,
.pfe-clipboard[copied] .pfe-clipboard__text {
  display: none !important;
}

:host(:not([copied])) .pfe-clipboard__text--success,
.pfe-clipboard:not([copied]) .pfe-clipboard__text--success {
  display: none !important;
}

::slotted([slot="icon"]),
.pfe-clipboard__icon > * {
  width: 100%;
}

#icon--url {
  display: none;
}

:host([copy-from="url"]) #icon--url {
  display: block;
}

:host([copy-from="url"]) #icon--copy {
  display: none;
}

:host([on="dark"]) {
  --pfe-clipboard--icon--Color: var(--pfe-clipboard--icon--Color--dark, var(--pfe-theme--color--text--muted--on-dark, #d2d2d2));
  --pfe-clipboard--icon--Color--hover: var(--pfe-clipboard--icon--Color--hover--dark, var(--pfe-theme--color--text--on-dark, #fff));
}

:host([on="saturated"]) {
  --pfe-clipboard--icon--Color: var(--pfe-clipboard--icon--Color--saturated, var(--pfe-theme--color--text--muted--on-saturated, #d2d2d2));
  --pfe-clipboard--icon--Color--hover: var(--pfe-clipboard--icon--Color--hover--saturated, var(--pfe-theme--color--text--on-saturated, #fff));
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  /* IE10+ */
  :host {
    display: inline-block;
  }
  .pfe-clipboard__icon {
    display: inline-block;
    margin-right: 0;
  }
  .pfe-clipboard__text {
    display: inline-block;
  }
  .pfe-clipboard__text--success {
    display: inline-block;
  }
}
